html {
    box-sizing: border-box;
    font-size: 13px;
    margin: 0;
    padding: 0;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    padding: 20px;
    background-color: $ltBlue;
    color: $black;
    font-family: $font-family;
    line-height: 1.3;
    max-width: 1200px;
    height: 100vh;
    margin: 0 auto;
}


.card {
    background-color: $white;
    color: $darkBlue;
    padding: 20px;
    margin: 10px;
    border-radius: 10px;

    .header {
        display: flex;
        align-items: center;

        img {
            border-radius: 100%;
            border: 2px solid $white;
            width: 3rem;
        }

        .person {
            margin-left: 1rem;
            h2 {
                display: inline;
            }
            span {
                display: block;
                opacity: 0.5;
            }
        }
    }

    .caption {
        font-size: 1.5rem;
        font-weight: 600;
    }

    .quote {
        font-weight: 500;
        opacity: 0.7;
    }
}

.card-one {
    background: top right no-repeat url(../img/bg-pattern-quotation.svg) $moderateViolet;
    color: $white;
}

.card-two {
    background-color: $veryDarkBlue;
    color: $white;
}

.card-four {
    background-color: $darkBlue;
    color: $white;
}

.attribution {
    font-size: 11px;
    text-align: center;
}
.attribution a {
    color: hsl(228, 45%, 44%);
}

/*Media Queries */

@media only screen and (min-width: 900px) {
    main {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: minmax(300px, auto);
        margin: 4rem 0;
    }

    .card-one {
        grid-column: 1/3;
        grid-row: 1;
    }

    .card-two {
        grid-column: 3;
        grid-row: 1;
    }

    .card-four {
        grid-column: 2/4;
        grid-row: 2;
    }

    .card-five {
        grid-column: 4;
        grid-row: 1/3;
    }
}
